import { Component, Prop, State,Element,Watch,h } from '@stencil/core';


type DataItem={
    idItem:string //for when selecting spesific item
    iconImage: string
    textData:string
    functionality: (...args:any) => any//if there is a functinality for this item on the Menu
    hasChildren:boolean
    children: DataItem [] //if it has children
    isDivisor:boolean
}

@Component({
    tag:"right-click-menu-v1",
    styleUrl:"right-click-menu-v1.css",
    shadow:true
})
export class RightClickMenuV1
{
    @Prop() theItems: DataItem[] = [];
    @State() menuVisible:boolean = false;

    @State() menuPosition: { x: number; y: number } = { x: 0, y: 0 };//the mouse click position

    @State() adjustedPosition: { x: number; y: number } = { x: 0, y: 0 };//the adjusted position of the menu to keep it within the viewport

    @State() submenuAdjustedPositionX: number = 250; //calculated X position for submenus (left or right of parent)

    @Element() hostElement!: HTMLElement;//dah bi3br 3n al <right-click-menu-v1>

    private menuElementRef!: HTMLUListElement; //dah represent al <ul>

    componentDidLoad() {//3mlna kida 3lshan ikon global Event Listener

        // Attach the right-click event listener to the host element
        this.hostElement.addEventListener('contextmenu', this.handleRightClick.bind(this));
        // Attach a click event listener to the entire document to close the menu
        // when the user clicks anywhere outside of it.
        document.addEventListener('click', this.handleClickOutside.bind(this));
    }

    disconnectedCallback() {//For memory leaks
        this.hostElement.removeEventListener('contextmenu', this.handleRightClick.bind(this));
        document.removeEventListener('click', this.handleClickOutside.bind(this));
    }

    @Watch('menuVisible')
    @Watch('menuPosition')
    adjustMenuPosition() {
        // Only adjust if the menu is visible and its DOM element is available
        if (this.menuVisible && this.menuElementRef) {
            const viewportWidth = window.innerWidth;
            const viewportHeight = window.innerHeight;
            const menuWidth = 260;
            const menuHeight = this.menuElementRef.offsetHeight; // Get actual height of the menu

            let xPos = this.menuPosition.x;
            let yPos = this.menuPosition.y;

            // Adjust X position: If menu goes off right edge, move it left
            if (xPos + menuWidth > viewportWidth) {
                xPos = viewportWidth - menuWidth;
            }
            // Adjust Y position: If menu goes off bottom edge, move it up
            if (yPos + menuHeight > viewportHeight) {
                yPos = viewportHeight - menuHeight;
            }

            // Update the adjusted position state, triggering a re-render
            this.adjustedPosition = { x: xPos, y: yPos };

            // Calculate submenu X position: If submenu would go off right edge,
            // position it to the left of its parent menu item.
            const submenuWidth = 150;
            this.submenuAdjustedPositionX = (xPos + menuWidth + submenuWidth > viewportWidth) ? -submenuWidth : menuWidth;
        }
    }

    //Show the custom menu
    handleRightClick(e: MouseEvent) {
        e.preventDefault();
        this.menuPosition = { x: e.clientX, y: e.clientY }; // store the location of the mouse click
        this.menuVisible = true; //dah hi5li al Watch tsht8l
    }

    //If clicked outside the the menu area
    handleClickOutside(e: MouseEvent) {
        if (this.menuVisible
            && this.menuElementRef //for safe check
            && !this.menuElementRef.contains(e.target as Node)//if it NOT inside the menu
        )
        {
            this.menuVisible = false;
        }
    }

    renderTheMenu(item:DataItem)
    {
        if(item.isDivisor)
        {
            return(<hr class = "menu-divider"></hr>)
        }

        return(<li
        id={item.idItem}
        class={{ 'has-submenu': item.hasChildren }}//if he has children this the class= 'has-submenu'
        onClick={() => {
                    // Execute the functionality if provided
                    if (item.functionality) {
                        item.functionality();
                    }
                    this.menuVisible = false; // Close the menu after an item is clicked
                }}
        >
        {item.iconImage && (
            <img src={item.iconImage} alt={item.iconImage ||'icon'}>
            </img>
        )}
        {item.textData}
        {item.hasChildren &&(
            <ul  class="submenu" style={{
                        top: '0px',
                        left: `${this.submenuAdjustedPositionX}px`, //left or right of the menu
                    }}>
                {item.children.map(child=> this.renderTheMenu(child))}
            </ul>
        )}
        </li>);
    }
    render(){
        return(<div  style={{ height: '100vh', width: '100vw', overflow: 'hidden', position: 'relative' }}>
        <ix-style-loader></ix-style-loader>
             <slot></slot>
            {this.menuVisible && (
                <ul
                        ref={(el) => this.menuElementRef = el as HTMLUListElement} //3lshan calculation when click
                        class="context-menu"
                        style={{
                            // ensure the menu doesn't go off the bottom of the viewport
                            top: `${this.adjustedPosition.y}px`,
                            left: `${this.adjustedPosition.x}px`
                        }}>
                    {this.theItems.map(i=>this.renderTheMenu(i))}
                </ul>
            )}
        </div>

        )
    }
}
